@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.imageCard {
    flex: 33.33%;
    text-align: center;
    position: relative;
}

.imageCard2 {
    flex: 33.33%;
    text-align: center;
    position: relative;
    visibility: hidden;
    width: 0;
}

.images{
    display: flex;
    overflow-x: hidden;
}

.responsiveIMG {
    width: 100%;
    height: auto;
}

.responsiveIMG2 {
    width: 100%;
    height: auto;
}

.imgTextTop {
    position: absolute;
    color: white;
    top: 100px;
    left: 45%;
    font-size: 2vw;
}

.pantText {
    position: absolute;
    color: white;
    bottom: 40px;
    font-size: 2vw;
    left: 48%;
}

.sweatshirtText{
    position: absolute;
    color: white;
    bottom: 40px;
    font-size: 2vw;
    left: 35%;
}

.FeaturedText {
    text-align: center;
    font-size: 1.5vw;
    padding-top: 2%;
}

.FeatureImages {
    text-align: center;
    padding: 2%;
}

.featureResponsiveIMG {
    height: auto;
    width: 30%;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 2%;
    border-radius: 20%;
    font-family: playfair display;
    transition-duration: 300ms;
}

.featureResponsiveIMG:hover {
    transform: scale(1.05);
}

.cotton {
    background-image: url('../../assets/cotton.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    padding: 10% 0 20% 5%;
    background-color: #000000;
    font-size: 2vw;
    color: black;
}

.cottonImgPhone {
    background-image: url('../../assets/CottonIMG.png');
    visibility: hidden;
    background-size: cover;
    width: 0px;
    height: 0px;
    padding: 0px;
    margin: 0px;
}

.about {
    text-align: center;
    background-color: black;
    padding: 5% 5% 0 5%;
}

.aboutText {
    text-align: center;
    padding-left: 30%;
    width: 100%;
    color: white;
    display: flex;
    font-size: 1vw;
}

.aboutSpacing {
    padding-right: 10%;
    text-align: center;
    font-weight: 300;
    cursor: pointer;
}

.sleeqMark {
    background-color: black;
    color: #ADADAD;
    padding: 1%;
    font-size: 80%;
}

.modal {
    height: fit-content;
    font-size: 200%;
    font-weight: 400;
    color: #eeeeee;
    background-color: #000000;
    border-top: 0.2vw solid #393E46;
}

.modal div h2 {
    margin-top: 1%;
    text-align: center;
    font-weight: 500;
}

.modal div p {
    margin-top: 2%;
    margin: 2% 15% 0 15%;
    padding-bottom: 3%;
}

.reveal {
    opacity: 0;
    transition: opacity 2s;
}

.reveal-visible {
    opacity: 1;
}


@media screen and (max-width: 480px) {

    .imageCard {
        visibility: hidden;
        width: 0px;
        padding: 0;
        margin: 0;
        height: 0px;
    }

    .imageCard2 {
        flex: 33.33%;
        text-align: center;
        position: relative;
        visibility: visible;
        width: 100%;
    }

    .imgTextTop {
        font-size: 50px;
        left: 30%;
    }

    .FeaturedText {
        font-size: 30px;
        font-weight: 100;
    }

    .cottonImgPhone {
        visibility: visible;
        width: 100vw;
        height: 80vh;
        background-color: rgb(208, 199, 199);
        left: 0px;
    }

    .cotton {
        display: none;
        width: 0px;
        height: 0px;
        font-size: 0px;
        padding: 0px;
        margin: 0px;
    }
    
    .sweatshirtText{
        visibility: hidden;
    }
    .responsiveIMG {
        visibility: hidden;
    }
    .responsiveIMG2 {
        width: 100%;
        flex: 100%;
    }
    .imageCard {
        flex: 0%;
        text-align: start;
    }

    .featureResponsiveIMG {
        height: auto;
        width: 90%;
        padding-left: 3%;
        padding-right: 3%;
        padding-bottom: 5%;
        border-radius: 20%;
    }
    
    .featureResponsiveIMG:hover {
        transform: scale(1);
    }

    .aboutText {
        font-size: 20px;
        display:table;
        padding: 0px 0px 10px 0px;
        margin: 0px;
    }

    .sleeqMark {
        font-size: 10px;
    }
}