@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
    overflow-y: visible;
}
.productCard {
    padding: 0 6% 0 6%;
}
.product-view {
    padding-top: 5%;
    margin-left: 5%;
    display: flex;
    overflow-y: "hidden",
}

.product-view-image {
    width: 40vw;
    min-height: 40vw;
    border-radius: 3%;
    background-color: #222831;
    transition: opacity 1s ease-in-out;
}

.fade {
    opacity: 0;
}

.image-container {
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .previous-button,
  .next-button {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    visibility: hidden;
    cursor: pointer;
  }
  
  .previous-button {
    left: 10px;
  }
  
  .next-button {
    right: 10px;
  }
  
  .image-container:hover .previous-button,
  .image-container:hover .next-button {
    visibility: visible;
  }
  

.product-view-info {
    width: 100%;
    text-align: center;

}

.size_wrap ul{
    display: flex;
    justify-content: center;
}
  
.size_wrap li{
    background-color: rgb(255, 255, 255);
    line-height: 6%;
    border-radius: 15%;
    border: 3px solid #222831;
    padding: 3%;
    cursor: pointer;
    transition: all 0.4s ease;
    list-style: none;
    font-weight: bolder;
    font-size: 120%;
}
  
.size_wrap li span{
    font-size: 100%;
}

.color_wrap {
    margin: 2% 25% 0 25%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3%;
    height: 150px;
  }

  .swipe-animation {
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  }

.product-name {
    font-size: 2vw;
    font-weight: 400;
}

.product-price {
    font-size: 2vw;
}

.product-description {
    padding: 7% 15% 0 20%;
    text-align: start;
}

.product-info{
    display: flex;
    justify-content: space-between;
    padding: 10% 20% 0 20%;
    align-items: baseline;
}

.handle-add-to-cart {
    width: 50%;
    background-color: #222831;
    padding: 10px;
    border-radius: 15px;
    color: rgb(227, 227, 227);
    cursor: pointer;
    margin-top: 2%;
    font-weight: 600;
    font-size: 1vw;
}

.circular-progress {
    margin-left: 50%;
    margin-top: 23vw; 
}

@media screen and (max-width: 700px) {
    .product-view {
        display:contents;
    }
    .product-view-image {
        width: 95vw;
        height: auto;
        border-radius: 3%;
        margin-left: 2.5vw;
        margin-top: 9vh;
    }

    .product-info{
        padding: 2% 5% 0 5%;
    }

    .product-name {
        font-size: 30px;
        font-weight: 400;
    }
    
    .product-price {
        font-size: 30px;
    }
    
    .product-description {
        padding: 2% 5% 0 5%;
        text-align: start;
        font-size: 50px;
        font-weight: 300;
    }

    .size_wrap li{
        padding: 7%;
    }

    .color_wrap {
        margin: 5% 0% 0 0%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 6%;
        height: 10vw;
      }

      .handle-add-to-cart {
        width: 80%;
        background-color: #222831;
        padding: 20px;
        border-radius: 45px;
        color: rgb(227, 227, 227);
        cursor: pointer;
        margin-top: 7%;
        font-weight: 600;
        font-size: 25px;
    }

    .circular-progress {
        margin-left: 48%;
        margin-top: 50vh; 
    }
    
    .previous-button,
    .next-button {
      visibility: visible;
    }
    
}

.navbar{
    display: flex;
    align-items: center;
    padding: 20px;
    position: fixed;
    z-index: 1000;
    background: rgba(245, 245, 245, 0.8);
    width: 100%;
    height: 65px;
    z-index:1000
}

.menu {
    visibility: hidden;
}

nav{
    flex: 1 1;
    text-align: center;
}

nav ul{
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    list-style-type: none;
}

nav ul li{
    display: inline-block;
    margin-right: 30px;
    margin-left: 30px;
    font-size: 20px;
    transition: font-size 500ms;
}

a{
    text-decoration: none;
    color: #000;
    transition: font-size 500ms;
}

nav ul li::after{
    content:"";
    width: 0%;
    height: 2px;
    background: rgb(29, 29, 29);
    display: block;
    margin: auto;
    transition: 500ms;
}

nav ul li:hover::after{
    width: 100%;
}

@media screen and (max-width: 900px) {
    nav ul li{
        visibility: hidden;
    }
    .menu {
        visibility: visible;
        padding-right: 5%;
        margin-left: 7%;
    }
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 0;
    top: 0;
    right: 0;
    background-color: rgba(245, 245, 245, 0.8);
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }
  
  .sidenav a {
    padding: 8px 32px 8px 8px;
    text-decoration: none;
    font-size: 25px;
    color: #000000;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    left: 25px;
    font-size: 36px;
    margin-right: 50px;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.imageCard {
    flex: 33.33% 1;
    text-align: center;
    position: relative;
}

.imageCard2 {
    flex: 33.33% 1;
    text-align: center;
    position: relative;
    visibility: hidden;
    width: 0;
}

.images{
    display: flex;
    overflow-x: hidden;
}

.responsiveIMG {
    width: 100%;
    height: auto;
}

.responsiveIMG2 {
    width: 100%;
    height: auto;
}

.imgTextTop {
    position: absolute;
    color: white;
    top: 100px;
    left: 45%;
    font-size: 2vw;
}

.pantText {
    position: absolute;
    color: white;
    bottom: 40px;
    font-size: 2vw;
    left: 48%;
}

.sweatshirtText{
    position: absolute;
    color: white;
    bottom: 40px;
    font-size: 2vw;
    left: 35%;
}

.FeaturedText {
    text-align: center;
    font-size: 1.5vw;
    padding-top: 2%;
}

.FeatureImages {
    text-align: center;
    padding: 2%;
}

.featureResponsiveIMG {
    height: auto;
    width: 30%;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 2%;
    border-radius: 20%;
    font-family: playfair display;
    transition-duration: 300ms;
}

.featureResponsiveIMG:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.cotton {
    background-image: url(/static/media/cotton.b3e2832c.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    padding: 10% 0 20% 5%;
    background-color: #000000;
    font-size: 2vw;
    color: black;
}

.cottonImgPhone {
    background-image: url(/static/media/CottonIMG.90d0abd4.png);
    visibility: hidden;
    background-size: cover;
    width: 0px;
    height: 0px;
    padding: 0px;
    margin: 0px;
}

.about {
    text-align: center;
    background-color: black;
    padding: 5% 5% 0 5%;
}

.aboutText {
    text-align: center;
    padding-left: 30%;
    width: 100%;
    color: white;
    display: flex;
    font-size: 1vw;
}

.aboutSpacing {
    padding-right: 10%;
    text-align: center;
    font-weight: 300;
    cursor: pointer;
}

.sleeqMark {
    background-color: black;
    color: #ADADAD;
    padding: 1%;
    font-size: 80%;
}

.modal {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 200%;
    font-weight: 400;
    color: #eeeeee;
    background-color: #000000;
    border-top: 0.2vw solid #393E46;
}

.modal div h2 {
    margin-top: 1%;
    text-align: center;
    font-weight: 500;
}

.modal div p {
    margin-top: 2%;
    margin: 2% 15% 0 15%;
    padding-bottom: 3%;
}

.reveal {
    opacity: 0;
    transition: opacity 2s;
}

.reveal-visible {
    opacity: 1;
}


@media screen and (max-width: 480px) {

    .imageCard {
        visibility: hidden;
        width: 0px;
        padding: 0;
        margin: 0;
        height: 0px;
    }

    .imageCard2 {
        flex: 33.33% 1;
        text-align: center;
        position: relative;
        visibility: visible;
        width: 100%;
    }

    .imgTextTop {
        font-size: 50px;
        left: 30%;
    }

    .FeaturedText {
        font-size: 30px;
        font-weight: 100;
    }

    .cottonImgPhone {
        visibility: visible;
        width: 100vw;
        height: 80vh;
        background-color: rgb(208, 199, 199);
        left: 0px;
    }

    .cotton {
        display: none;
        width: 0px;
        height: 0px;
        font-size: 0px;
        padding: 0px;
        margin: 0px;
    }
    
    .sweatshirtText{
        visibility: hidden;
    }
    .responsiveIMG {
        visibility: hidden;
    }
    .responsiveIMG2 {
        width: 100%;
        flex: 100% 1;
    }
    .imageCard {
        flex: 0% 1;
        text-align: start;
    }

    .featureResponsiveIMG {
        height: auto;
        width: 90%;
        padding-left: 3%;
        padding-right: 3%;
        padding-bottom: 5%;
        border-radius: 20%;
    }
    
    .featureResponsiveIMG:hover {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    .aboutText {
        font-size: 20px;
        display:table;
        padding: 0px 0px 10px 0px;
        margin: 0px;
    }

    .sleeqMark {
        font-size: 10px;
    }
}
