.card-media-container {
    position: relative;
  }
  
  .card-media-container:hover .trashcan-icon {
    opacity: 1;
  }

  .card-media-container:hover #image-card{
    -webkit-filter: blur(5px);
            filter: blur(5px);
  }
  
  .trashcan-icon {
    background-size: cover;
    -webkit-transform: scale(4);
            transform: scale(4);
    position: absolute;
    top: 50%;
    right: 48%;
    opacity: 0;
    color: #de0e0e;
    cursor: pointer;
    transition: 0.5s;
  }
