.product-view {
    padding-top: 5%;
    margin-left: 5%;
    display: flex;
    overflow-y: "hidden",
}

.product-view-image {
    width: 40vw;
    min-height: 40vw;
    border-radius: 3%;
    background-color: #222831;
    transition: opacity 1s ease-in-out;
}

.fade {
    opacity: 0;
}

.image-container {
    position: relative;
    width: fit-content;
  }
  
  .previous-button,
  .next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    visibility: hidden;
    cursor: pointer;
  }
  
  .previous-button {
    left: 10px;
  }
  
  .next-button {
    right: 10px;
  }
  
  .image-container:hover .previous-button,
  .image-container:hover .next-button {
    visibility: visible;
  }
  

.product-view-info {
    width: 100%;
    text-align: center;

}

.size_wrap ul{
    display: flex;
    justify-content: center;
}
  
.size_wrap li{
    background-color: rgb(255, 255, 255);
    line-height: 6%;
    border-radius: 15%;
    border: 3px solid #222831;
    padding: 3%;
    cursor: pointer;
    transition: all 0.4s ease;
    list-style: none;
    font-weight: bolder;
    font-size: 120%;
}
  
.size_wrap li span{
    font-size: 100%;
}

.color_wrap {
    margin: 2% 25% 0 25%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3%;
    height: 150px;
  }

  .swipe-animation {
    transition: transform 0.5s ease-in-out;
  }

.product-name {
    font-size: 2vw;
    font-weight: 400;
}

.product-price {
    font-size: 2vw;
}

.product-description {
    padding: 7% 15% 0 20%;
    text-align: start;
}

.product-info{
    display: flex;
    justify-content: space-between;
    padding: 10% 20% 0 20%;
    align-items: baseline;
}

.handle-add-to-cart {
    width: 50%;
    background-color: #222831;
    padding: 10px;
    border-radius: 15px;
    color: rgb(227, 227, 227);
    cursor: pointer;
    margin-top: 2%;
    font-weight: 600;
    font-size: 1vw;
}

.circular-progress {
    margin-left: 50%;
    margin-top: 23vw; 
}

@media screen and (max-width: 700px) {
    .product-view {
        display:contents;
    }
    .product-view-image {
        width: 95vw;
        height: auto;
        border-radius: 3%;
        margin-left: 2.5vw;
        margin-top: 9vh;
    }

    .product-info{
        padding: 2% 5% 0 5%;
    }

    .product-name {
        font-size: 30px;
        font-weight: 400;
    }
    
    .product-price {
        font-size: 30px;
    }
    
    .product-description {
        padding: 2% 5% 0 5%;
        text-align: start;
        font-size: 50px;
        font-weight: 300;
    }

    .size_wrap li{
        padding: 7%;
    }

    .color_wrap {
        margin: 5% 0% 0 0%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 6%;
        height: 10vw;
      }

      .handle-add-to-cart {
        width: 80%;
        background-color: #222831;
        padding: 20px;
        border-radius: 45px;
        color: rgb(227, 227, 227);
        cursor: pointer;
        margin-top: 7%;
        font-weight: 600;
        font-size: 25px;
    }

    .circular-progress {
        margin-left: 48%;
        margin-top: 50vh; 
    }
    
    .previous-button,
    .next-button {
      visibility: visible;
    }
    
}
