.navbar{
    display: flex;
    align-items: center;
    padding: 20px;
    position: fixed;
    z-index: 1000;
    background: rgba(245, 245, 245, 0.8);
    width: 100%;
    height: 65px;
    z-index:1000
}

.menu {
    visibility: hidden;
}

nav{
    flex: 1;
    text-align: center;
}

nav ul{
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    list-style-type: none;
}

nav ul li{
    display: inline-block;
    margin-right: 30px;
    margin-left: 30px;
    font-size: 20px;
    transition: font-size 500ms;
}

a{
    text-decoration: none;
    color: #000;
    transition: font-size 500ms;
}

nav ul li::after{
    content:"";
    width: 0%;
    height: 2px;
    background: rgb(29, 29, 29);
    display: block;
    margin: auto;
    transition: 500ms;
}

nav ul li:hover::after{
    width: 100%;
}

@media screen and (max-width: 900px) {
    nav ul li{
        visibility: hidden;
    }
    .menu {
        visibility: visible;
        padding-right: 5%;
        margin-left: 7%;
    }
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 0;
    top: 0;
    right: 0;
    background-color: rgba(245, 245, 245, 0.8);
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }
  
  .sidenav a {
    padding: 8px 32px 8px 8px;
    text-decoration: none;
    font-size: 25px;
    color: #000000;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    left: 25px;
    font-size: 36px;
    margin-right: 50px;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }