.option_size {
    background-color: #222831;
    color: white;
    line-height: 6%;
    border-radius: 15%;
    padding: 6%;
    transition: all 0.4s ease;
    list-style: none;
    font-weight: 900;
}

/* .item-card :hover{
    filter: blur(5px);
} */

.card-media-container {
    position: relative;
  }
  
  .card-media-container:hover .trashcan-icon {
    opacity: 1;
  }

  .card-media-container:hover #image-card{
    filter: blur(5px);
  }
  
  .trashcan-icon {
    background-size: cover;
    transform: scale(4);
    position: absolute;
    top: 50%;
    right: 48%;
    opacity: 0;
    color: #de0e0e;
    cursor: pointer;
    transition: 0.5s;
  }